import React, { useState, useEffect } from 'react';
import './Header.css';
import logoEN from '../../assets/bosta-logoEN.png';
import logoAR from '../../assets/bosta-logoAR.png';

import SearchBar from '../SearchBar/SearchBar';
import i18n from '../../i18n';

function Header() {
  const [language, setLanguage] = useState(localStorage.getItem('language') || 'English');
  const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth <= 768);
  const [logo, setLogo] = useState(logoEN);

  const handleLanguageChange = (e) => {
    const selectedLanguage = e.target.value;
    setLanguage(selectedLanguage);
    localStorage.setItem('language', selectedLanguage);
    i18n.changeLanguage(selectedLanguage === 'Arabic' ? 'AR' : selectedLanguage === 'French' ? 'FR' : 'EN');
    if (selectedLanguage === 'Arabic') {
      setLogo(logoAR);
    }  else {
      setLogo(logoEN);
    }  };

  const updateLogo = (lang) => {
    if (lang === 'Arabic') {
      setLogo(logoAR);
    }  else {
      setLogo(logoEN);
    }
  };

  useEffect(() => {
    const storedLanguage = localStorage.getItem('language');
    if (storedLanguage) {
      setLanguage(storedLanguage);
      i18n.changeLanguage(storedLanguage === 'Arabic' ? 'AR' : storedLanguage === 'French' ? 'FR' : 'EN');
      // updateLogo(storedLanguage);
      if (storedLanguage === 'Arabic') {
        setLogo(logoAR);
      }  else {
        setLogo(logoEN);
      }
    }

    const handleResize = () => {
      setIsSmallScreen(window.innerWidth <= 768);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <header className="header">
      <div className="header-container">
        <img src={logo} alt="Bosta Logo" className="header-logo" />
        
        <div className="header-right">
          <div className="header-search-bar">
            {isSmallScreen && <SearchBar />}
          </div>

          <select
            className="language-dropdown"
            value={language}
            onChange={handleLanguageChange}
          >
            <option value="English">English</option>
            <option value="Arabic">عربي</option>
            <option value="French">Français</option>
          </select>
        </div>
      </div>
    </header>
  );
}

export default Header;